.home {     
    display: flex;     
    justify-content: space-between;     
    align-items: flex-start;     
    padding: 4rem 10%;     
    background-color: #f4f4f4;     
    min-height: 100vh;     
    width: 100%;     
    box-sizing: border-box; 
}  

.left-side {     
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 3rem;
    padding-right: 6rem;
    max-width: 600px;
    padding-top: 2rem;
}  

.text-content {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
}

.left-side h1 {     
    font-size: 2.5rem;     
    color: #333;     
    margin: 0;
    line-height: 1.2;
}  

.running-lines {     
    font-size: 1.1rem;     
    color: black;     
    font-style: italic;     
    margin: 0;
    line-height: 1.6;
}  

.right-side {     
    flex: 1;     
    display: flex;     
    justify-content: flex-end;     
    align-items: flex-start;
    padding-top: 2rem;
}  

.profile-image {     
    width: 380px;     
    height: 380px;     
    border-radius: 50%;     
    border: 5px solid #333;     
    object-fit: cover;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}  

.name {     
    color: #3498db;     
    font-weight: bold;
    display: inline-block;
}  

.social-icons {     
    display: flex;
    gap: 3rem;
}  

.icon-circle {     
    display: flex;     
    justify-content: center;     
    align-items: center;     
    width: 38px;     
    height: 38px;     
    border-radius: 50%;     
    background-color: #f0f0f0;     
    transition: background-color 0.3s, transform 0.3s;     
    text-decoration: none;     
    color: #333;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}  

.icon-circle:hover {     
    background-color: #3498db;     
    transform: scale(1.1);     
    color: white; 
}

/* Responsive Design */
@media screen and (max-width: 1200px) {
    .home {
        padding: 4rem 5%;
    }
    
    .profile-image {
        width: 350px;
        height: 350px;
    }
    
    .left-side {
        padding-right: 4rem;
    }
}

@media screen and (max-width: 992px) {
    .home {
        flex-direction: column;
        padding: 4rem 5%;
        gap: 3rem;
        align-items: center;
        text-align: center;
    }
    
    .left-side {
        padding-right: 0;
        align-items: center;
        padding-top: 0;
    }
    
    .text-content {
        align-items: center;
    }
    
    .social-icons {
        justify-content: center;
    }
    
    .right-side {
        justify-content: center;
        padding-top: 0;
    }
}

@media screen and (max-width: 576px) {
    .home {
        padding: 3rem 1rem;
    }
    
    .left-side h1 {
        font-size: 2.2rem;
    }
    
    .running-lines {
        font-size: 1rem;
    }
    
    .profile-image {
        width: 280px;
        height: 280px;
    }
    
    .icon-circle {
        width: 35px;
        height: 35px;
    }
    
    .social-icons {
        gap: 2rem;
    }
}
